@import "vars";
@import "mixins";

.nav {
  margin-left: 43px;
  &__list {
    list-style: none;
    display: flex;
  }

  &__item {
    margin-left: 63px;
    font-weight: 700;

    &:last-child {
      margin-left: 135px;
    }
  }
  &__link {
    outline-color: rgba($color: #fff, $alpha: 0.3);
    color: white;
    transition: color 0.7s;

    &:hover {
      color: #fcc304;
    }

    &--active {
      outline-color: rgba($color: #fff, $alpha: 0.3);
      color: white;
      margin-left: 4px;
      transition: color 0.7s;

      &:hover {
        color: #fcc304;
      }

      &::after {
        content: "";
        height: 4px;
        width: 64px;
        display: block;
        background-color: #fcc304;
        border-radius: 2px;
      }
    }
  }
}

@media screen and (max-width: 1025px) {
  .nav {
    &__item {

      &:last-child {
        margin-left: 65px;
      }

    }
  }
}

@media screen and (max-width: 999px) {
  .nav {
    margin-left: 0;

    &__list {
      flex-direction: column;
    }
    &__item {
      margin: auto;

      &:last-child {
        margin: auto;
      }
    }
  }

}
